body{
    background-image: url("./assets/gif/background.gif");  
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
#root{
    background-color: rgba(0, 139, 134, 0.187);
}


/*main page*/

.main__show{
    display: flex;
    flex-direction: column;
    height: 460px;
    justify-content: space-between;
}
.show__information--container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}
.title__title--temperature{
    font-size: 6rem;
    margin-left: 30px;
}
.place__title{
    font-size: 2rem;
}
.information__city--container, .information__draw--container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.draw__image--container{
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.image__image--draw{
    width: 100%;
    height: 100%;
    margin-bottom: 10px;
}
.main__features{
    box-shadow: 0px -19px 23px -2px #000000;
}


/*Search page*/

.features__search{
    max-height: 275px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.649);
}
.search__form--container{
    width: 100%;
}
.form__form--search{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.location__input{
    margin: 40px 0px 0px 30px;
    background: none;
    border: none;
    border-bottom: 1px solid rgba(128, 128, 128, 0.511);
    color: white;
}
.submit__button--form{
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 139, 134);
    border: none;
    cursor: pointer;
}
.submit__button--form:hover{
    background-color: rgb(0, 139, 65);   
    transition: 0.3s;
}
.submit__button--form:focus, .location__input{
    outline: none;
}
.button__image--submit{
    width: 35px;
    height: 35px;
    background-image: url("./assets/icons/lupa.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.search__results--container{
    width: 100%;
    margin-top: 30px;
    overflow: scroll;
}
::-webkit-scrollbar {
    display: none;
}
.results__list{
    list-style: none;
}
.list__search{
    color: gray;
    cursor: pointer;
    margin-bottom: 20px;
}


/*details page*/

.features__details{
    min-height: 275px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.649);
}
.details__weather--container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.weather__title--container{
    width: 80%;
    padding: 30px 0px;
    border-top: 1px solid gray;
}
.weather__information--container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.information__list, .information__list--value{
    list-style: none;
}
.list__information{
    color: gray;
    margin-bottom: 20px;
}
.list__information--value{
    color: white;
    margin: 0px 30px 20px;
}


/*media queries*/

@media (min-width: 1023px){
    
    #root{
        display: flex;
        flex-direction: row;
    }
    .main__show{
        height: 100vh;
        width: 65%;
    }
    .show__information--container{
        flex-direction: row;
        justify-content: flex-start;
        margin: 0px 0px 70px 50px
    }
    .information__draw--container{
        margin: 0px 10px;
    }
    .draw__image--container{
        width: 46px;
        height: 46px;
    }
    .main__features{
        width: 35%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        box-shadow: -19px 6px 23px -2px #000000;

    }
    .features__search, .features__details{
        width: 100%;
        height: 50%;
    }
    .features__search{
        max-height: 50%;
    }
    .search__results--container{
        margin-bottom: 50px;
    }
    .weather__information--container{
        margin-top: 30px;
    }
}